import { Typography } from '@mui/material';
import type { GridColDef } from '@mui/x-data-grid';
import { AbilitiesCell } from 'components/cells/AbilitiesCell';
import { RemoveCell } from 'components/cells/RemoveCell';
import { StatCell } from 'components/cells/StatCell';
import { TypesCell } from 'components/cells/TypesCell';
import type { Ability, Type } from 'models/models';
import React from 'react';

interface getDataGridColumnsProps {
	abilitiesWithDescriptions: Ability[],
	types: Type[],
	pokemonList: string[],
	setPokemonList: React.Dispatch<React.SetStateAction<string[]>>,
	setSnackbarOpen: React.Dispatch<React.SetStateAction<boolean>>,
	setSnackbarMessage: React.Dispatch<React.SetStateAction<string>>,
	setSnackbarColor: React.Dispatch<React.SetStateAction<'success' | 'info' | 'warning' | 'error'>>
}

export const getDataGridColumns = (
	{
		abilitiesWithDescriptions,
		types,
		pokemonList,
		setPokemonList,
		setSnackbarOpen,
		setSnackbarMessage,
		setSnackbarColor
	} : getDataGridColumnsProps
): GridColDef[] => {
	return [
		{
			field: 'remove',
			headerName: '',
			width: 75,
			headerAlign: 'center',
			headerClassName: 'header',
			align: 'center',
			renderCell: (param) => (
				<RemoveCell
					name={param.row.originalName}
					pokemonList={pokemonList}
					setPokemonList={setPokemonList}
					setSnackbarOpen={setSnackbarOpen}
					setSnackbarMessage={setSnackbarMessage}
					setSnackbarColor={setSnackbarColor}
				/>
			)
		},
		{
			field: 'name',
			headerName: 'Name',
			type: 'string',
			width: 250,
			headerAlign: 'center',
			headerClassName: 'header',
			align: 'center',
			renderCell: (param) => <Typography>{param.value}</Typography>
		},
		{
			field: 'sprite',
			headerName: 'Sprite',
			type: 'string',
			width: 125,
			headerAlign: 'center',
			headerClassName: 'header',
			align: 'center',
			renderCell: (param) => <img src={param.value} alt='sprite' />
		},
		{
			field: 'types',
			headerName: 'Types',
			type: 'array',
			width: 200,
			headerAlign: 'center',
			headerClassName: 'header',
			align: 'center',
			renderCell: (param) => <TypesCell typeStrings={param.value} types={types} />
		},
		{
			field: 'abilities',
			headerName: 'Abilities',
			type: 'array',
			width: 150,
			headerAlign: 'center',
			headerClassName: 'header',
			align: 'center',
			renderCell: (param) => <AbilitiesCell abilityStrings={param.value} abilities={abilitiesWithDescriptions} />
		},
		{
			field: 'hp',
			headerName: 'HP',
			type: 'number',
			width: 165,
			headerAlign: 'center',
			headerClassName: 'header',
			align: 'center',
			renderCell: (param) => <StatCell statValue={param.value} />
		},
		{
			field: 'attack',
			headerName: 'Attack',
			type: 'number',
			width: 165,
			headerAlign: 'center',
			headerClassName: 'header',
			align: 'center',
			renderCell: (param) => <StatCell statValue={param.value} />
		},
		{
			field: 'defense',
			headerName: 'Defense',
			type: 'number',
			width: 165,
			headerAlign: 'center',
			headerClassName: 'header',
			align: 'center',
			renderCell: (param) => <StatCell statValue={param.value} />
		},
		{
			field: 'specialAttack',
			headerName: 'Special Attack',
			type: 'number',
			width: 165,
			headerAlign: 'center',
			headerClassName: 'header',
			align: 'center',
			renderCell: (param) => <StatCell statValue={param.value} />
		},
		{
			field: 'specialDefense',
			headerName: 'Special Defense',
			type: 'number',
			width: 165,
			headerAlign: 'center',
			headerClassName: 'header',
			align: 'center',
			renderCell: (param) => <StatCell statValue={param.value} />
		},
		{
			field: 'speed',
			headerName: 'Speed',
			type: 'number',
			width: 165,
			headerAlign: 'center',
			headerClassName: 'header',
			align: 'center',
			renderCell: (param) => <StatCell statValue={param.value} />
		},
	];
};
